body {
  @apply bg-white bg-no-repeat bg-contain text-black;
  background-position: bottom;
  background-position-y: 140%;
}

#login {
  width: 370px;
}
#login #login_error {
  color: #000;
}
#login .message {
  color: #000;
}
#login h1 a {
  width: 90px;
  height: 44px;
  background-size: 90px 44px;
}
#login h1 a:focus {
  box-shadow: none;
}
#login form {
  @apply shadow-none p-10 border-white/0 bg-white;
}
#login form label {
  @apply text-xs text-black mb-2 font-primary font-thin uppercase leading-normal tracking-widest;
}
#login form .input {
  @apply w-full border-2 border-white border-b-red py-3 px-2 font-primary text-base text-black;
}
#login form .input::placeholder {
  @apply font-primary text-gray-300 text-base;
}
#login form .input:focus-visible {
  @apply outline-black/5 rounded-none;
}
#login form input[type=submit] {
  @apply font-primary font-light text-red text-base transition-all duration-500 ease-in-out inline-flex justify-center items-center bg-white uppercase py-2 px-11 border border-red rounded-none tracking-widest mt-8 hover:text-white hover:bg-red;
}
#login #backtoblog, #login #nav {
  @apply font-primary font-light text-center text-black;
}
#login #backtoblog a, #login #nav a {
  @apply font-primary font-light text-black;
}
#login input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 30px white inset;
  -webkit-text-fill-color: #000 !important;
}

.wpml-login-ls form#wpml-login-ls-form select#wpml-language-switcher-locales {
  @apply rounded-none border border-solid uppercase text-xs font-primary font-semibold transition-all duration-300 ease-in hover:bg-white;
}
.wpml-login-ls form#wpml-login-ls-form input.button {
  @apply rounded-none border border-solid uppercase text-xs font-primary font-semibold px-4 py-1 transition-all duration-300 ease-in hover:bg-white;
}